.MainNavbar{
    display: flex;
    width: 100%;
    height: 55px;
    /* border: 1px solid red; */
    justify-content: flex-end;
    align-items: center;
    background-color: var(--purple);
    box-sizing: border-box;
    position: absolute;
    top: 0%;
    left: 0%;
}

.NavbarHolder{
    display: flex;
    gap: 10px;
    justify-content: flex-end;
    /* border: 1px solid red; */
    align-items: center;
    background-color: var(--purple);
    /* padding: 7px; */
    font-size: 18px;
    width: 50%;   
}

.NavbarHolder a{
    color: var(--sky-blue);
    padding: 10px;
    border-radius: 10px;
}

.NavbarHolder a:hover{
    background: var(--dark-blue);
}



@media screen and (max-width:480px) {
    .NavbarHolder{
        gap: 5px;
        font-size: 12px;
    }
}